.wpcf7-form {
    position: relative;

    span.wpcf7-spinner {
        position: absolute;
        bottom: 0;
    }

    span.form-violator {
        font-size: 0.83rem;
        padding-top: 1.6em;
    }

    .form-footer {
        align-items: center;
        display: flex;
        flex-flow: column nowrap;
        padding-top: 4.89em;
        position: relative;

        @include break(mobile) {
            padding-top: 3em;
        }

        input[type="submit"] {
            width: max-content;

            @include break(mobile) {
                width: 100%;
            }
        }
    }

    .wpcf7-response-output {
        border: 0 !important;
        color: #dc3232 !important;
        margin: 0 !important;
        text-align: center;
        width: 100%;
    }

    & div.subscription {
        align-items: center;
        display: flex;
        flex-flow: row wrap;
        gap: 1em;
    
        &-heading {
    
            @include break(tabletxl) {
                padding-bottom: 1.88em;
                width: 100%;
            }
            
            &__wrapper {
                margin-right: 2em;
    
                h2 {
                    color: #fff;
                    font-size: 1.8rem;
                    margin-bottom: 0.3em;
                }
    
                p {
                    color: #fff;
                    line-height: 1.6;
                    margin-bottom: 0;
                }
            }
        }
    
        &-form {
            flex-grow: 1;
    
            @include break(tabletxl) {
                padding-bottom: 0.94em;
                width: 100%;
            }
    
            &__wrapper {
                display: flex;
                position: relative;
    
                @include break(phablet) {
                    flex-flow: row wrap;
                }
    
                span.wpcf7-form-control-wrap {
                    flex-grow: 1;
                    padding-right: 0.8em;
    
                    @include break(phablet) {
                        padding-right: 0;
                        padding-bottom: 0.94em;
                        width: 100%;
                    }
    
                    input {
                        border-radius: 4px;
                        border: 0;
                        color: #555;
                        height: 3.2rem;
                        padding: 16px;
                        transition: 200ms all ease-out;
                        width: 100%;
    
                        &:focus {
                            outline: none !important;
                            box-shadow: 0 0 10px rgba(#000, 0.32);
                        }
                    }
                }
    
                input[type="submit"] {
                    height: 3.2rem;
                    padding: 0 2.78em;
                } 
    
                span.wpcf7-spinner {
                    position: absolute;
                    top: 0;
                    right: 0;
                }
                
                span.wpcf7-spinner {
                    margin: 0;
                    position: absolute;
                    top: 50%;
                    transform: translateY(-50%);

                    @include break(phablet) {
                        top: 75%;
                    }
                }

                span.wpcf7-not-valid-tip {
                    color: #fff;
                    font-size: 0.7rem;
                    position: absolute;
                    top: -1.25rem;
                }
            }
        }

        & + div.wpcf7-response-output {
            border: 0;
            bottom: -18px;
            color: #fff;
            font-size: 0.7rem;
            margin: 0;
            padding: 0;
            position: absolute;
            right: 0;
        }
    }

    
}

.wpcf7 form.sent .wpcf7-response-output {
    color: #46b450 !important;
}